* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
}

.header__logo, .menu__list {
  align-items: center;
  display: flex;
}

.menu__item {
  margin-left: 20px;
  list-style-type: none;
}

.menu__item:first-child {
  margin-left: 0;
}

.menu__link {
  color: #000;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14.06px;
  text-decoration: none;
  display: flex;
}

.menu__link:hover {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 37px;
  height: 4px;
  text-decoration: underline;
  position: absolute;
  bottom: 0;
  left: 0;
}

.is-active {
  color: #00acdc;
  position: relative;
}
